import React from 'react';
import { bool, func, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input';
const CSS_STATE = 'vds-state';

const InputContainer = ({
  children,
  className,
  css,
  disabled,
  expanded,
  filled,
  focus,
  invalid,
  isTextarea,
  readOnly,
  ...remainingProps
}) => (
  <div
    className={classnames({
      [`${CSS_PREFIX}`]: !isTextarea,
      [`${CSS_PREFIX}-textarea-container`]: Boolean(isTextarea),
      [`${CSS_STATE}--disabled`]: Boolean(disabled),
      [`${CSS_STATE}--error`]: Boolean(invalid),
      [`${CSS_STATE}--expanded`]: Boolean(expanded),
      [`${CSS_STATE}--filled`]: Boolean(filled),
      [`${CSS_STATE}--focus`]: Boolean(focus),
      [`${CSS_STATE}--readonly`]: Boolean(readOnly),
      [`${CSS_STATE}--success`]: typeof invalid === 'boolean' && !invalid
    }, className)}
    css={css}
    {...remainingProps}
  >
    {children}
  </div>
);

InputContainer.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  css: func,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Filled
   */
  filled: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * isTextarea
   */
  isTextarea: bool,

  /**
   * ReadOnly
   */
  readOnly: bool
};

InputContainer.displayName = 'InputContainer';

export default InputContainer;
