import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-input';

const InputInput = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

InputInput.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

InputInput.displayName = 'InputInput';

export default InputInput;
