import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-sr';

const Sr = forwardRef(({ children, className, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

Sr.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

Sr.defaultProps = {
  tag: 'span'
};

Sr.displayName = 'Sr';

export default Sr;
