import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-count';

const InputCount = ({
  ariaAtomic,
  ariaLive,
  children,
  className,
  ...remainingProps
}) => (
  <div
    aria-atomic={ariaAtomic}
    aria-live={ariaLive}
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

InputCount.propTypes = {
  /**
  * Aria atomic
  */
  ariaAtomic: string,

  /**
  * Aria Live
  */
  ariaLive: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

InputCount.defaultProps = {
  ariaAtomic: 'true',
  ariaLive: 'polite'
};

InputCount.displayName = 'InputCount';

export default InputCount;
