import React from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-message';
const CSS_STATE = 'vds-state';

const InputMessage = ({
  ariaAtomic,
  children,
  className,
  invalid,
  role,
  show,
  ...remainingProps
}) => (
  <span
    aria-atomic={ariaAtomic}
    aria-live={invalid ? 'assertive' : 'polite'}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--hidden`]: !show,
      [`${CSS_STATE}--error`]: Boolean(invalid)
    }, className)}
    role={role}
    {...remainingProps}
  >
    {children}
  </span>
);

InputMessage.propTypes = {
  /**
   * Aria Atomic
   */
  ariaAtomic: oneOf(['true', 'false']),

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Role
   */
  role: string,

  /**
   * Show
   */
  show: bool
};

InputMessage.defaultProps = {
  ariaAtomic: 'true',
  role: 'alert'
};

InputMessage.displayName = 'InputMessage';

export default InputMessage;
