import React, { forwardRef, Fragment, useEffect, useState } from 'react';
import { bool, func, node, number, object, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import Icon from '../icon';
import InputBar from '../input-bar';
import InputContainer from '../input-container';
import InputCount from '../input-count';
import InputCountNumber from '../input-count-number';
import InputInput from '../input-input';
import InputLabel from '../input-label';
import InputMessage from '../input-message';
import InputTextarea from '../input-textarea';

const getHeight = (rows, el) => {
  const {
    borderBottomWidth,
    borderTopWidth,
    lineHeight,
    paddingBottom,
    paddingTop
  } = window.getComputedStyle(el);

  const rowHeight =
    rows === null
      ? 0
      : parseFloat(lineHeight) * parseInt(rows, 10) +
        parseFloat(borderBottomWidth) +
        parseFloat(borderTopWidth) +
        parseFloat(paddingBottom) +
        parseFloat(paddingTop);

  const scrollHeight = el.scrollHeight + parseFloat(borderBottomWidth) + parseFloat(borderTopWidth);
  return Math.max(rowHeight, scrollHeight);
};

const resize = (rows, el) => {
  if (el) {
    el.style.height = '0';
    el.style.height = `${getHeight(rows, el)}px`;
  }
};

const Textarea = forwardRef(({
  ariaLabel,
  autoResize,
  className,
  containerProps,
  counter,
  css,
  disabled,
  errorIcon,
  errorText,
  helpIcon,
  helpText,
  id = shortid.generate(),
  invalid,
  label,
  maxCharacters,
  noValidate,
  onBlur,
  onChange,
  onFocus,
  readOnly,
  rows,
  showCounter,
  showErrorText,
  showHelpText,
  ...remainingProps
}, ref) => {
  const [count, setCount] = useState(
    remainingProps.value
      ? remainingProps.value.length
      : remainingProps.defaultValue
        ? remainingProps.defaultValue.length
        : 0
  );
  const [dirty, setDirty] = useState(false);
  const [filled, setFilled] = useState(Boolean(remainingProps.value) || Boolean(remainingProps.defaultValue));
  const [focused, setFocused] = useState(false);
  const [isInvalid, setIsInvalid] = useState(invalid);
  const [rId] = useState(id);

  const setValidity = e => {
    if (!noValidate) {
      if (dirty) {
        setIsInvalid((!e.target.checkValidity() || (e.target.required && !e.target.value.trim().length)));
      }

      if (!dirty && (e.target.required && !e.target.value.trim().length)) {
        setIsInvalid(true);
      }

      if (dirty && !e.target.required && !e.target.value.trim().length) {
        setIsInvalid(undefined);
      }
    }
  };

  useEffect(() => {
    setFilled(Boolean(remainingProps.defaultValue) || Boolean(remainingProps.value));
    setCount(
      remainingProps.defaultValue
        ? remainingProps.defaultValue.length
        : remainingProps.value
          ? remainingProps.value.length
          : 0
    );
  }, [remainingProps.defaultValue, remainingProps.value]);

  useEffect(() => {
    setIsInvalid(invalid);
  }, [invalid]);

  const handleBlur = e => {
    setFocused(false);
    setValidity(e);

    if (typeof onBlur === 'function') {
      onBlur(e);
    }
  };

  const handleChange = e => {
    setFilled(e.target.value !== '');
    setCount(e.target.value.length);

    if (typeof isInvalid === 'boolean') {
      setValidity(e);
    }
    if (autoResize) {
      resize(rows, e.target);
    }

    setDirty(true);
    if (typeof onChange === 'function') {
      onChange(e);
    }
  };

  const handleFocus = e => {
    setFocused(true);

    if (typeof onFocus === 'function') {
      onFocus(e);
    }
  };

  return (
    <InputContainer
      className={classnames(className)}
      css={css}
      disabled={disabled}
      filled={filled}
      focus={focused}
      readOnly={readOnly}
      isTextarea={!autoResize}
      invalid={isInvalid}
      {...containerProps}
    >
      {label ? <InputLabel htmlFor={rId}>{label}</InputLabel> : undefined}
      <InputBar>
        <InputInput>
          <InputTextarea
            aria-describedby={
              showCounter
                ? `count-${rId}`
                : showErrorText || showHelpText
                  ? `input-message-${rId}`
                  : undefined}
            aria-label={label ? undefined : ariaLabel}
            aria-invalid={isInvalid}
            id={rId}
            disabled={disabled}
            onBlur={handleBlur}
            onChange={handleChange}
            onFocus={handleFocus}
            readOnly={readOnly}
            ref={ref}
            {...remainingProps}
          />
        </InputInput>
      </InputBar>
      <InputMessage
        id={`input-message-${rId}`}
        show={(showHelpText && Boolean(helpText)) || (showErrorText && Boolean(errorText))}
        invalid={isInvalid}
      >
        {
          !isInvalid && focused && showHelpText && Boolean(helpText)
            ? <Fragment>{helpIcon} {helpText}</Fragment>
            : undefined
        }
        {
          isInvalid === true && showErrorText && Boolean(errorText)
            ? <Fragment>{errorIcon} {errorText}</Fragment>
            : undefined
        }
      </InputMessage>
      {
        showCounter && maxCharacters
          ? <InputCount
            id={`count-${rId}`}
          >
            {counter(parseInt(maxCharacters, 10), count)}
          </InputCount>
          : undefined
      }
    </InputContainer>
  );
});

Textarea.propTypes = {
  /**
   * Aria Label
   */
  ariaLabel: string,

  /**
   * Auto resize
   */
  autoResize: bool,

  /**
   * @ignore
   */
  className: string,

  /**
   * Container Props
   */
  containerProps: object, // eslint-disable-line

  /**
   *  Counter
   */
  counter: func,

  /**
   * @ignore
   */
  css: func,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   *  Error Message Icon
   */
  errorIcon: node,

  /**
   *  Error Message
   */
  errorText: node,

  /**
   * Input Help Message Icon
   */
  helpIcon: node,

  /**
   * Input Help Message
   */
  helpText: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Label
   */
  label: node,

  /**
   * Max Characters
   */
  maxCharacters: oneOfType([number, string]),

  /**
   * Disable native HTML5 validation e.g. type=email, pattern="some regex pattern"
   */
  noValidate: bool,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * On Change
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * ReadOnly
   */
  readOnly: bool,

  /**
   * Rows
   */
  rows: oneOfType([number, string]),

  /**
   * Show Counter
   */
  showCounter: bool,

  /**
   * Show Counter
   */
  showErrorText: bool,

  /**
   * Show Help Message
   */
  showHelpText: bool
};

Textarea.defaultProps = {
  counter: (max, count) => <span>Characters remaining <InputCountNumber>{max - count}</InputCountNumber></span>,
  errorIcon: <Icon name="error" resolution="low" />
};

Textarea.displayName = 'Textarea';

export default Textarea;
